// 
export function deepClone(target) {
  // 
  let result;
  // 
  if (typeof target === 'object') {
  // 
      if (Array.isArray(target)) {
          result = []; // result
          for (let i in target) {
              // 
              result.push(deepClone(target[i]))
          }
       // nullnull
      } else if(target===null) {
          result = null;
       // RegExp    
      } else if(target.constructor===RegExp){
          result = target;
      }else {
       // for in
          result = {};
          for (let i in target) {
              result[i] = deepClone(target[i]);
          }
      }
   // 
  } else {
      result = target;
  }
   // 
  return result;
}

export function stringToArray(arr) {
  return arr && arr.length > 0 ? arr.split(',') : null
}

export function setCookie (key, value, day) {
  let expires = day * 86400 * 1000  //  ms
  let date = new Date( + new Date() + expires) // 
  document.cookie = `${key}=${value};expires=${date.toUTCString()}`
}

export function getCookie(cname){
  var name = cname + "=";
  var ca = document.cookie.split(';');
  for(var i=0; i<ca.length; i++) 
  {
    var c = ca[i].trim();
    if (c.indexOf(name)===0) return c.substring(name.length,c.length);
  }
  return "";
}

//
export function downFile(res, fileName) {
  const blob = new Blob( [res], {type: "application/x-xls;charset=utf-8"} )
  // URL
  const url = window.URL || window.webkitURL || window.moxURL
  // 
  const downloadHref = url.createObjectURL(blob)
  // a
  let downloadLink = document.createElement("a")
  downloadLink.href = downloadHref
  downloadLink.download = fileName
  // 
  downloadLink.click()
  window.URL.revokeObjectURL(url);
}

/**
 * 
 * gist: 
 * keykey
 * dataIndex: 
 * val: 
 * return bool
 */
export function isEffectiveEditor(gist, key, dataIndex, val) {
  if (gist.length === 0) {
    return false
  }
 
  return gist.some(item => {
    if (item.key === key) {
      return item[dataIndex] === val
    }
    return false
  })
}
/**
 * 
 * @param gist 
 * @param newObj 
 */
export function isFit(gist, newObj) {
  for (let i = 0; i < gist.length;i++){
    if (gist[i].key === newObj.key) {
      for (let key in gist[i]) {
        if (newObj[key] !== gist[i][key]) {
          return false
        }
      }
      return true
    }
  }
}

/**
 * 
 * @param {*} dataArr : 
 * @param {*} id 
 * @param {*} key key
 * @param {*} val 
 * true:  
 */
export function isRepeat(dataArr, id, key, val) {
  if (dataArr.length > 0) {
    dataArr.some(item => item.id !== id && item[key] === val)
  }
}

export function hasKey(dataList, key, val) {
  dataList.some()
}

/**
 * key
 * @param {*} dataList 
 * @param {*} key 
 * @param {*} val 
 */
export function callObj(dataList, key, val) {
  let i = dataList.filter(item => item[key] + "" === val)
  return i[0]
}


//
export function debounce(fn, wait, immediate = false){
  let timer, startTimeStamp = 0;
  let context, args;
 
  let run = (timerInterval) => {
    timer = setTimeout(() => {
      let now = (new Date()).getTime();
      let interval = now - startTimeStamp
      if (interval < timerInterval) {
        startTimeStamp = now;
        run(wait - interval);
      } else {
        if (!immediate) {
          fn.apply(context, args);
        }
        clearTimeout(timer);
        timer = null;
      }
			
    }, timerInterval);
  }
 
  return function () {
    context = this;
    args = arguments;
    let now = (new Date()).getTime();
    startTimeStamp = now;
 
    if (!timer) {
      if (immediate) {
        fn.apply(context, args);
      }
      run(wait);
    }
  }
}